<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.unick" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="filters.uuid" placeholder="ID"></el-input>
                </el-form-item>
                <el-form-item label="" prop="room_type">
                    <el-select v-model="filters.u_type" clearable placeholder="">
                        <el-option
                                v-for="item in uTypeArr"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker
                            v-model="daterange"
                            type="daterange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            :picker-options="pickerOptions2"
                            @change="dateChange">
                    </el-date-picker>
                </el-form-item>
                <el-form-item prop="room_type">
                    <el-select v-model="filters.type" clearable placeholder="">
                        <el-option
                                v-for="item in typeArr"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="loadData"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
                  style="width: 100%;">
            <el-table-column prop="uid" label="" width="80">
            </el-table-column>
            <el-table-column prop="uuid" label="UUID" width="80">
            </el-table-column>
            <el-table-column prop="unick" label="" width="100">
            </el-table-column>
            <el-table-column prop="uphone" label="" width="100">
            </el-table-column>
            <el-table-column label="" width="100">
                <template slot-scope="scope">
                    <div v-if="scope.row.uface==='error'">
                        <img src="https://raw.githubusercontent.com/taylorchen709/markdown-images/master/vueadmin/user.png"
                             alt="" height="50px"/>
                    </div>
                    <div v-else>
                        <img :src="scope.row.uface" alt="" height="50px"/>
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="uemail" label="" width="120">
            </el-table-column>
            <el-table-column prop="usex" label="" width="80">
                <!--:formatter="formatSex"-->
            </el-table-column>
            <el-table-column prop="uvip" label="vip" width="90">
            </el-table-column>
            <el-table-column prop="u_token" label="OpenId" width="120">
            </el-table-column>
            <el-table-column prop="u_type" label="" width="80">
            </el-table-column>
            <el-table-column prop="status" label="" width="100">
            </el-table-column>
            <el-table-column prop="regist_ip" label="IP" width="120">
            </el-table-column>
            <el-table-column prop="regist_time" label="" width="120">
            </el-table-column>
            <el-table-column prop="login_ip" label="IP" width="120">
            </el-table-column>
            <el-table-column prop="login_time" label="" width="120">
            </el-table-column>
            <el-table-column prop="ugold" label="" width="100">
            </el-table-column>
            <el-table-column prop="ubank" label="()" width="120">
            </el-table-column>
            <el-table-column prop="ucard" label="" width="100">
            </el-table-column>
            <el-table-column prop="roomid" label="" width="120">
            </el-table-column>
            <el-table-column fixed="right" label="" width="120">
                <template slot-scope="scope">
                    <el-button @click="handleDel(scope.$index, scope.row)" type="text" size="small">
                        
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20"
                           :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import userInfoApi from '../../api/user'

    export default {
        data() {
            return {
                uTypeArr: [{
                    value: '',
                    label: ''
                }, {
                    value: '0',
                    label: ''
                }, {
                    value: '1',
                    label: 'QQ'
                }, {
                    value: '2',
                    label: ''
                }],
                typeArr: [{
                    value: '3',
                    label: ''
                }, {
                    value: '2',
                    label: ''
                }, {
                    value: '1',
                    label: ''
                }],

                pickerOptions2: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                filters: {
                    unick: '',
                    uuid: '',
                    u_type: '',
                    type: '',
                    start_time: '',
                    end_time: '',
                },
                users: [],
                daterange: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//

            }
        },
        mounted() {
            this.loadData()
        },
        methods: {
            //
            loadData() {
                let para = {
                    page: this.page,
                    uuid: this.filters.uuid,
                    u_type: this.filters.u_type,
                    type: this.filters.type,
                    start_time: this.filters.start_time,
                    end_time: this.filters.end_time,
                };

                if (this.filters.unick !== '') {
                    para.unick = this.filters.unick
                }
                this.listLoading = true;
                userInfoApi.getUser(para, (res) => {
                    this.users = res.data.list;
                    this.total = parseInt(res.data.count);
                    this.listLoading = false;
                }).catch((error => {
                    console.log(error)
                }))
            },
            // //
            // formatSex: function (row, column) {
            //     return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
            // },
            handleCurrentChange(val) {
                this.page = val;
                this.loadData();
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    userInfoApi.delUser(row.uid, (res) => {
                        this.listLoading = false;
                        this.$notify({
                            message: res.data.msg,
                        });
                        this.loadData();
                    });
                }).catch(() => {
                    this.$notify({
                        message: '',
                    });
                });
            },
            //
            dateChange() {
                if (this.daterange !== null && this.daterange.length > 0) {
                    let [startTime, endTime] = this.daterange;
                    startTime != null && (this.filters.start_time = util.formatDates(startTime));
                    endTime != null && (this.filters.end_time = util.formatDates(endTime))
                } else {
                    this.filters.start_time = '';
                    this.filters.end_time = ''
                }
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
        },
    }

</script>

<style scoped>

</style>


